import { ModelViewCoreWrapper } from '..';

export class ViewWrapper {
    modelViewCoreWrapper: ModelViewCoreWrapper;

    divHost: HTMLElement;
    div: HTMLElement;

    constructor(modelViewCoreWrapper: ModelViewCoreWrapper, id: string, width: number = 100, height: number = 100) {
        this.modelViewCoreWrapper = modelViewCoreWrapper;
        this.InitDiv(id, width, height);
        this.AppendEvent();
    }
    InitDiv(id: string, width: number, height: number) {
        const divNJslabSandbox = document.createElement('div');
        divNJslabSandbox.style.position = 'absolute';

        this.div = document.createElement('div');
        divNJslabSandbox.appendChild(this.div);

        const divHost = document.getElementById(id);
        if (divHost) {
            this.divHost = divHost;
            if (this.divHost.parentNode) { this.divHost.appendChild(divNJslabSandbox); }
        }

        this.div.id = 'NCanvas';
        this.div.style.width = this.divHost.clientWidth + 'px';
        this.div.style.height = this.divHost.clientHeight + 'px';
        // this.div.style.background = 'red';
        // this.div.style.pointerEvents = 'none';
        this.div.style.zIndex = '1';
    }
    AppendEvent() {
        window.addEventListener('resize', () => {
            this.div.style.width = this.divHost.clientWidth + 'px';
            this.div.style.height = this.divHost.clientHeight + 'px';
            // this.modelViewCoreWrapper.model.app.Resize(this.divHost.clientWidth, this.divHost.clientHeight );
        }, true);
    }
}
